  border-top: none;
  max-height: 380px;
  margin: 0;
  overflow-y: scroll;
  overflow-x: hidden;
  padding: 0;
  background-color: var(--bg0);
  z-index: 3;
}

.searchable dl dt {
  // color: var(--accent1);
