setfilter {

}

setfilter>div {
  /* width: 1200px; */
  /* height: 300px; */
  /* background: white; */
  /* border: 1px solid var(--accent1); */
  max-width: 85% !important;
}

/* Search bar */
setfilter .search {
  width: 100%;
  height: 40px;
  padding-left: 5px;
}


/* Toggle button */
setfilter .toggle {
  border: 1px solid var(--accent1);
  background: transparent;
  margin-bottom: 4px;
  margin-right: 4px;
  padding: 2px;
  cursor: pointer;
  text-align: center;
  min-width: 50px;
  height: 35px;
}
setfilter .toggle label {
  cursor: pointer;
  margin: 0px;
}

/* Checkbox input */
setfilter input[type="checkbox"] {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
}
setfilter .checkbox {
  width: 15px;
  height: 15px;
  background: var(--bg0);
  border: 1px solid var(--accent1);
  padding: 2px;
}
setfilter .checkbox:checked {
  background: var(--accent1);
}


/* title labels */
setfilter .title {
  font-size: 25px;
  font-weight: bold;
  /* margin-left: 5px; */
}

/* block list */
setfilter .blocklist {

}
setfilter .blocklist .block {
  margin-bottom: 10px;
}
setfilter .blocklist .block .blocktitle {
  font-size: 20px;
  color: var(--front0);
  background: var(--bg0);
  border-color: var(--bg0);
  width: 100%;
  /* margin-left: 5px; */
  /* font-weight: bold; */
}
setfilter .blocklist .block .blocktitle:focus {
  border-color: var(--bg0) !important;
}
setfilter .blocklist .block .mod {
  margin-bottom: 10px;
}
setfilter .blocklist .block .mod label {
  background: var(--bg1);
  width: 65%;
  /* margin-left: 5px;
  margin-right: 5px; */
  margin-bottom: 0px
  /* background: var(--bg1); */
  /* font-size: 20px; */
  /* font-weight: bold; */
}

setfilter .btnAdd {
  width: 100%;
}

/* left side */
.leftside {
  padding-left: 1px;
}
/* right side */
.rightside {
  padding-right: 1px;
}
