/* class="itemsheet"  */
/* Maintenant on target l'lment "itemsheet" ! */
itemsheet {
  --sheetaccent: var(--accent1);
  --sheetaccenthover: var(--accent0);
  /* background: #1d3030; */
  /* background: var(--bg1); */
  cursor: pointer;
  /* border: 1px solid var(--sheetaccent); */
  border: 1px solid transparent;
  /* box-shadow: 5px 5px 5px black; */
}
/* immediate child must fill the itemsheet size for clicks */
itemsheet .itemsheet {
  /* width: 100%;
  height: 100%;
  align-items: stretch; */
  min-height: 150px;

  background-repeat: no-repeat;
  /* background-attachment: fixed; */
  background-position: center;

}
itemsheet .itemsheet::after {
  opacity: 0.5;
}

itemsheet .bgimg {
  display: inline;
  /* width: 100%; */
  /* height: 100%; */
  /* min-height: 150px; */
  opacity: 0.5;
  background-repeat: no-repeat;
  /* background-attachment: fixed; */
  background-position: center;
}

itemsheet img {
  width: 55px;
  height: 55px;
}

itemsheet .title {
  padding: 5px;
  /* background: var(--sheetaccent); */
  color: var(--sheetaccent);
  font-size: 1.1rem;
}
itemsheet .statistics {
  padding: 5px;
}
itemsheet .statistics ul {
    list-style: none;
    margin: 0px;
    padding: 0px;
}
itemsheet .statistic {

}
itemsheet .conditions {
  /* border-top: 1px solid var(--sheetaccent); */
}


itemsheet:hover {
  border: 1px solid var(--sheetaccenthover);
}
itemsheet:hover .title {
  color: var(--sheetaccenthover);
  /* background: var(--sheetaccenthover); */
}
itemsheet:hover .conditions {
  border-top: 1px solid var(--sheetaccenthover);
}

